import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { PostEvalutionSettingsService } from 'app/shared/services/post-evalution-settings.service';
import { trigger, style, animate, transition } from '@angular/animations';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-post-evaluation-page',
  templateUrl: './post-evaluation-page.component.html',
  styleUrls: ['./post-evaluation-page.component.scss'],
  animations: [
    trigger(
      'enterAnimation', [
      transition(':enter', [
        style({ transform: 'translateX(100%)' }),
        animate('300ms', style({ transform: 'translateX(0)' }))
      ]),
      transition(':leave', [
        style({ transform: 'translateX(0)' }),
        animate('300ms', style({ transform: 'translateX(100%)' }))
      ])
    ]
    )
  ],
})
export class PostEvaluationPageComponent implements OnInit, OnDestroy {
  
  
  campaignSettings;
  compaignId;
  openSettings;
  private routeSub: Subscription;

  constructor(
    private postEvalutionSettingsService: PostEvalutionSettingsService,
    private route: ActivatedRoute,
    private chRef: ChangeDetectorRef
  ) {
    postEvalutionSettingsService.open.subscribe(res => {
      this.openSettings = res;
    })
  }

  ngOnInit() {
    this.routeSub = this.route.params.subscribe(params => {
      console.log(params['id']);
      this.compaignId = params['id'];
      this.chRef.detectChanges();
    });
  }
  
  getSettings(settings){
    console.log(settings, 'campaign settings');
    this.campaignSettings = settings;
  }

  ngOnDestroy(): void {
    this.routeSub.unsubscribe()
  }

}
