import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class PostEvalService {
  url = 'http://swagger.dashboard.api.spotcheck.airplaycontrol.com:10080';

  constructor(private http: HttpClient) { }

  getByWeeks(campignSettings) {
    let params = 'campaignUuid='+campignSettings.campaignUuid+'&sourceTypeUuid='+campignSettings.sourceTypeUuid+'&audiencetargetUuid='+campignSettings.audiencetargetUuid+'&startTimestampUtc='+campignSettings.startTimestampUtc+'&endTimestampUtc='+campignSettings.endTimestampUtc+'&fullWeeksOnly='+campignSettings.fullWeeksOnly;
    return this.http.get(this.url + '/ByWeeks?' + params);
  }
  getByDealerships(campignSettings) {
    let params = 'campaignUuid='+campignSettings.campaignUuid+'&sourceTypeUuid='+campignSettings.sourceTypeUuid+'&audiencetargetUuid='+campignSettings.audiencetargetUuid+'&startTimestampUtc='+campignSettings.startTimestampUtc+'&endTimestampUtc='+campignSettings.endTimestampUtc+'&fullWeeksOnly='+campignSettings.fullWeeksOnly;
    return this.http.get(this.url + '/ByDealerships?' + params);
  }
  getBySources(campignSettings) {
    let params = 'campaignUuid='+campignSettings.campaignUuid+'&sourceTypeUuid='+campignSettings.sourceTypeUuid+'&audiencetargetUuid='+campignSettings.audiencetargetUuid+'&startTimestampUtc='+campignSettings.startTimestampUtc+'&endTimestampUtc='+campignSettings.endTimestampUtc+'&fullWeeksOnly='+campignSettings.fullWeeksOnly;
    return this.http.get(this.url + '/BySources?' + params);
  }
  getDayToDay(campignSettings) {
    let params = 'campaignUuid='+campignSettings.campaignUuid+'&sourceTypeUuid='+campignSettings.sourceTypeUuid+'&audiencetargetUuid='+campignSettings.audiencetargetUuid+'&startTimestampUtc='+campignSettings.startTimestampUtc+'&endTimestampUtc='+campignSettings.endTimestampUtc+'&fullWeeksOnly='+campignSettings.fullWeeksOnly;
    return this.http.get(this.url + '/DayToDay?' + params);
  }

}
