import { Component, OnInit, ViewChild, AfterViewInit, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { MatTableDataSource, MatPaginator } from '@angular/material';
import { MatDialog } from '@angular/material/dialog';
import { ModalVideoComponent } from 'app/shared/components/modals/modal-video/modal-video.component';
import { CampaignService } from 'app/shared/services/main/campaign.service';
import { Observable } from 'rxjs';
import 'rxjs/Rx'
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

declare var Chart;
import * as moment from 'moment';

@Component({
  selector: 'app-dashboard-page',
  templateUrl: './dashboard-page.component.html',
  styleUrls: ['./dashboard-page.component.scss']
})
export class DashboardPageComponent implements OnInit, AfterViewInit, OnDestroy {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;

  playsStats;
  campaignsList;
  public mockedData = [];
  dataSource;

  observableList: any;

  constructor(
    public dialog: MatDialog,
    private campaignService: CampaignService,
    private chRef: ChangeDetectorRef,
    private router: Router,
    public translate: TranslateService
  ) {

    this.campaignService.getCampaignsList().subscribe(campaignsData => {
      console.log(campaignsData);
      this.campaignsList = campaignsData;
      this.observableList = [];
      this.getObservableList(this.campaignsList);
      console.log(this.observableList);
      Observable.forkJoin(this.observableList).subscribe(t => {
        t.forEach(element => {
          console.log(element);
          element['playButton'] = ''
          element['firstPlay'] = moment(element['firstPlay']).format('YYYY-MM-DD HH:mm');
          this.mockedData.push(element)
        });
        this.dataSource = new MatTableDataSource(this.mockedData);
        this.dataSource.paginator = this.paginator;
        console.log(this.mockedData);
      });
      this.chRef.detectChanges();
    });

    this.campaignService.getPlaysStatistics().subscribe(data => {
      console.log(data);
      this.playsStats = data;
      this.chRef.detectChanges();
    });
  }

  getObservableList(data) {
    data.forEach(element => {
      this.observableList.push(this.campaignService.getCompaignStatistics(element.campaignUuid))
    });
    return this.observableList;
  }

  columnsToDisplay = ['playButton', 'campaignName', 'companyName',
    'brandName', 'productName',
    'spotVersionsCount', 'firstPlay'
  ];
  columnsName = {
    playButton: '',
    campaignName: 'Campaign',
    companyName: 'Company',
    brandName: 'Brand',
    productName: 'Product',
    spotVersionsCount: 'Spots',
    firstPlay: 'Creation Date'
  };
  expandedElement;
  ngOnDestroy(): void {
    // this.translate.onLangChange.unsubscribe();
  }
  ngAfterViewInit() {
    this.translate.getTranslation(this.translate.currentLang).subscribe((res: string) => {
      this.columnsName = {
        playButton: '',
        campaignName: res['Campaign'],
        companyName: res['Company'],
        brandName: res['Brand'],
        productName: res['Product'],
        spotVersionsCount: res['Spots'],
        firstPlay: res['Creation Date']
      };
    });
    this.translate.onLangChange.subscribe(res => {
      this.columnsName = {
        playButton: '',
        campaignName: res.translations['Campaign'],
        companyName: res.translations['Company'],
        brandName: res.translations['Brand'],
        productName: res.translations['Product'],
        spotVersionsCount: res.translations['Spots'],
        firstPlay: res.translations['Creation Date']
      };
      // this.chRef.detectChanges();
    });

    setTimeout(() => {
      var ctx = document.getElementById('myChart');
      let data = {
        labels: ["January", "February", "March", "April", "May", "June", "July"],
        datasets: [
          {
            label: "My First dataset",
            fill: false,
            lineTension: 0.1,
            backgroundColor: "rgba(75,192,192,0.4)",
            borderColor: "rgba(75,192,192,1)",
            borderCapStyle: 'butt',
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: 'miter',
            pointBorderColor: "rgba(75,192,192,1)",
            pointBackgroundColor: "#fff",
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: "rgba(75,192,192,1)",
            pointHoverBorderColor: "rgba(220,220,220,1)",
            pointHoverBorderWidth: 2,
            pointRadius: 5,
            pointHitRadius: 10,
            data: [65, 59, 80, 0, 56, 55, 40],
          }
        ]
      };
      var myLineChart = new Chart(ctx, {
        type: 'line',
        data: data,
        width: '100%',
        height: '300px',
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: true
              }
            }]
          }
        }
      });
    }, 100);
  }
  ngOnInit() {
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(ModalVideoComponent, {
      width: '600px',
      data: 'data to export'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
    });
  }

  goToPostEvalutionPage(element) {
    let index = this.mockedData.indexOf(element);
    let campaignId = this.campaignsList[index].campaignUuid
    console.log(this.campaignsList[index]);
    this.router.navigate(['/home/post-evaluation/' + campaignId]);
  }

}
