import { Component, ViewChild, Input, OnInit, ChangeDetectorRef, Output, EventEmitter, AfterViewInit } from "@angular/core";
import { MatPaginator, MatTableDataSource } from "@angular/material";
import { PostEvalutionSettingsService } from "app/shared/services/post-evalution-settings.service";
import { CampaignService } from "app/shared/services/main/campaign.service";
import { PostEvalService } from "app/shared/services/main/post-eval.service";
import { Location } from '@angular/common'
import { Router } from "@angular/router";
import { AudienceService } from "app/shared/services/main/audience.service";
import { SourceTypeService } from "app/shared/services/main/source-type.service";
import { TranslateService } from "@ngx-translate/core";

import { forkJoin, Observable } from 'rxjs';
import 'rxjs/Rx'
import * as moment from 'moment';

@Component({
  selector: 'app-capmaigns-settings',
  templateUrl: './capmaigns-settings.component.html',
  styleUrls: ['./capmaigns-settings.component.scss'],
})
export class CampaignsSettingsComponent implements OnInit, AfterViewInit {

  @Input('compaignId') selectedCompaignId;
  @Output('getSettings') getSettings = new EventEmitter;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild('channelsTabGroup', { static: false }) channelsTabGroup;

  campaignSettings = {};

  campaign;
  selectedCampaign;
  campaignsList;
  observableList: any;
  dataSource;
  mockedData;
  sourceType;
  playsStationsList;

  audiencetargetUuid;
  sourceTypeUuid;
  startTimestampUtc;
  endTimestampUtc;
  fullWeeksOnly;
  selectedInterval = 'This month';
  selectedTarget;
  selectedChannel;

  intevalLables: any;

  constructor(
    private postEvalutionSettingsService: PostEvalutionSettingsService,
    private campaignService: CampaignService,
    private postEvalService: PostEvalService,
    private chRef: ChangeDetectorRef,
    private location: Location,
    private router: Router,
    private audienceService: AudienceService,
    private sourceTypeService: SourceTypeService,
    public translate: TranslateService,
  ) { }

  target = [
    { value: 'Target Entity', viewValue: 'Target Entity' },
    { value: 'Cases ', viewValue: 'Cases ' }
  ];
  filterChannelsTv = [
    { value: 'All channels', viewValue: 'All channels' },
    { value: 'National channels ', viewValue: 'National channels' },
    { value: 'Sky channels ', viewValue: 'Sky channels' },
    { value: 'Mediaset Premium channels ', viewValue: 'Mediaset Premium channels' },
    { value: 'Local channels ', viewValue: 'Local channels' }
  ];
  filterChannelsRadio = [
    { value: 'All channels', viewValue: 'All channels' },
    { value: 'National channels', viewValue: 'National channels' },
    { value: 'Local channels ', viewValue: 'Local channels ' }
  ];

  dateIntervals = [
    {
      value: 'This month',
      viewValue: 'This month',
      startUtc: '',
      endUtc: '',
      isFinished: false,
    },
    {
      value: 'Last 3 months',
      viewValue: 'Last 3 months',
      startUtc: '',
      endUtc: '',
      isFinished: false,
    },
    {
      value: 'Last 6 months',
      viewValue: 'Last 6 months',
      startUtc: '',
      endUtc: '',
      isFinished: false,
    },
    {
      value: 'Last 12 months',
      viewValue: 'Last 12 months',
      startUtc: '',
      endUtc: '',
      isFinished: false,
    }
  ];

  columnsToDisplay = ['campaignName', 'companyName',
    'brandName', 'productName'
  ];
  columnsName = {
    campaignName: 'Campaign name',
    companyName: 'Campany name',
    brandName: 'Company',
    productName: 'Brand'
  }
  expandedElement;

  ngOnInit(): void {
    console.log('start');
    forkJoin([
      this.getAudienceTargetsList(),
      this.initSelectedCampaign(),
      this.getCampaignsListTable(),
      this.getSourceType(),
      this.initTimeInterval()
    ]).subscribe(t => {
      setTimeout(() => {
        this.changeSelectedTab();
      }, 100);
      this.campaignSettings = {
        campaignUuid: this.selectedCompaignId,
        sourceTypeUuid: this.selectedChannel.sourceTypeUuid,
        audiencetargetUuid: this.audiencetargetUuid,
        startTimestampUtc: this.startTimestampUtc,
        endTimestampUtc: this.endTimestampUtc,
        fullWeeksOnly: this.fullWeeksOnly
      };
      this.getSettings.emit(this.campaignSettings);
      console.log('end');
    });
  }
  getSourceType() {
    return new Promise(resolve => {
      this.sourceTypeService.getSourceType().subscribe(data => {
        this.sourceType = data;
        this.chRef.detectChanges();
        resolve('getSourceType')
      });
    })
  }
  cangeInterval() {
    this.dateIntervals.forEach((element, index) => {
      if (!element.isFinished && element.value === 'Last flight') {
        element.endUtc = moment(new Date).toISOString();
        element.viewValue = 'From ' + moment(element.startUtc).format('YYYY-MM-DD HH:mm') + " to " + moment(new Date).format('YYYY-MM-DD HH:mm'),
          // this.dateIntervals[index].endUtc = moment(new Date).format('YYYY-MM-DD HH:mm:ss')
          console.log(element.endUtc);
        console.log(this.dateIntervals);
      }
    });
  }
  getDateIntervalsOptions(flight?) {
    console.log(flight, 'flightflightflightflightflight');

    this.dateIntervals = [];
    if (flight.length) {
      this.dateIntervals = [
        {
          value: 'This month',
          viewValue: this.intevalLables['This month'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        },
        {
          value: 'Last 3 months',
          viewValue: this.intevalLables['Last 3 months'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        },
        {
          value: 'Last 6 months',
          viewValue: this.intevalLables['Last 6 months'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        },
        {
          value: 'Last 12 months',
          viewValue: this.intevalLables['Last 12 months'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        }
      ];
      flight.forEach(element => {
        let obj = {
          value: 'Last flight',
          startUtc: element.startUtc,
          endUtc: element.endUtc,
          isFinished: element.isFinished,
          viewValue: 'From ' + moment(element.startUtc).format('YYYY-MM-DD HH:mm') + " to " + moment(element.endUtc).format('YYYY-MM-DD HH:mm'),
        }
        this.dateIntervals.unshift(obj)
      });

    } else {
      this.dateIntervals = [];
      this.dateIntervals = [
        {
          value: 'This month',
          viewValue: this.intevalLables['This month'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        },
        {
          value: 'Last 3 months',
          viewValue: this.intevalLables['Last 3 months'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        },
        {
          value: 'Last 6 months',
          viewValue: this.intevalLables['Last 6 months'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        },
        {
          value: 'Last 12 months',
          viewValue: this.intevalLables['Last 12 months'],
          startUtc: '',
          endUtc: '',
          isFinished: false,
        }
      ];
    }
  }
  initSelectedCampaign() {
    return new Promise(resolve => {
      if (this.selectedCompaignId) {
        this.campaignService.getCampaign(this.selectedCompaignId).subscribe(data => {
          this.campaignService.getCompaignStatistics(data['campaignUuid']).subscribe(res => {
            this.selectedCampaign = res;
          });
          this.campaignService.getCompaignPlaysStationsWithPlaysCount(this.selectedCompaignId).subscribe(playsStations => {
            console.log(playsStations, 'getCompaignPlaysStationsWithPlaysCount');
            this.playsStationsList = playsStations;
            this.selectedChannel = this.playsStationsList[0];
            this.chRef.detectChanges();
          });
          this.campaignService.getCompaignFlights(this.selectedCompaignId).subscribe(data => {
            console.log(data);
            this.getDateIntervalsOptions(data);
            resolve('if (this.selectedCompaignId)')
          });
        });
      } else {
        Observable.forkJoin([this.campaignService.getCampaignsList()]).subscribe(t => {
          this.campaignService.getCompaignStatistics(t[0][0]['campaignUuid']).subscribe(res => {
            this.selectedCampaign = res;
            console.log(this.selectedCampaign);
          })
          this.chRef.detectChanges();
          this.location.go(this.router.url + '/' + t[0][0]['campaignUuid']);
          this.selectedCompaignId = t[0][0]['campaignUuid'];
          this.campaignService.getCompaignFlights(this.selectedCompaignId).subscribe(data => {
            console.log(data);
            this.getDateIntervalsOptions(data);
          });
          this.campaignService.getCompaignPlaysStationsWithPlaysCount(this.selectedCompaignId).subscribe(playsStations => {
            this.playsStationsList = playsStations;
            this.selectedChannel = this.playsStationsList[0];
            resolve('if (!this.selectedCompaignId)')
            this.chRef.detectChanges();
          });

        });
      }
    })
  }

  initTimeInterval() {
    return new Promise(resolve => {
      let dateStart = new Date;
      let dateEnd = new Date;
      dateStart.setMonth(dateStart.getMonth() - 1);
      this.startTimestampUtc = moment(dateStart).toISOString();
      this.endTimestampUtc = moment(dateEnd).toISOString();
      this.fullWeeksOnly = false;
      resolve('initTimeInterval')
    })
  }

  getTimeInterval(e) {
    let dateStart = new Date;
    let dateEnd = new Date;
    if (e.value === 'Last flight') {
      this.startTimestampUtc = e.startUtc;
      this.endTimestampUtc = e.endUtc;
      this.fullWeeksOnly = false;
    } else if (e.value === 'This month') {
      dateStart.setMonth(dateStart.getMonth() - 1);
      this.startTimestampUtc = moment(dateStart).toISOString();
      this.endTimestampUtc = moment(dateEnd).toISOString();
      this.fullWeeksOnly = false;
    } else if (e.value === 'Last 3 months') {
      dateStart.setMonth(dateStart.getMonth() - 3);
      this.startTimestampUtc = moment(dateStart).toISOString();
      this.endTimestampUtc = moment(dateEnd).toISOString();
      this.fullWeeksOnly = true;
    } else if (e.value === 'Last 6 months') {
      dateStart.setMonth(dateStart.getMonth() - 6);
      this.startTimestampUtc = moment(dateStart).toISOString();
      this.endTimestampUtc = moment(dateEnd).toISOString();
      this.fullWeeksOnly = true;
    } else if (e.value === 'Last 12 months') {
      dateStart.setMonth(dateStart.getMonth() - 12);
      this.startTimestampUtc = moment(dateStart).toISOString();
      this.endTimestampUtc = moment(dateEnd).toISOString();
      this.fullWeeksOnly = true;
    };
    this.onChange();
  }

  getCampaignsListTable() {
    return new Promise(resolve => {
      this.campaignService.getCampaignsList().subscribe(campaignsData => {
        console.log(campaignsData);
        this.campaignsList = campaignsData;
        this.observableList = [];
        this.getObservableList(this.campaignsList);
        console.log(this.observableList);
        Observable.forkJoin(this.observableList).subscribe(t => {
          this.mockedData = [];
          t.forEach(element => {
            this.mockedData.push(element)
          });
          this.dataSource = new MatTableDataSource(this.mockedData);
          this.dataSource.paginator = this.paginator;

          resolve('getCampaignsList')
        });
        this.chRef.detectChanges();
      });
    })
  }

  getAudienceTargetsList() {
    return new Promise(resolve => {
      this.audienceService.getAudience().subscribe(data => {
        console.log(data, 'getAudienceTargetsList');
        this.target = [];
        let arr: any;
        arr = data;
        arr.forEach(element => {
          this.target.push(element)
        });
        this.audiencetargetUuid = data[0]['audiencetargetUuid'];
        this.selectedTarget = data[0]['name'];
        resolve('this.audiencetargetUuid')
      })
    })
  }

  getObservableList(data) {
    data.forEach(element => {
      this.observableList.push(this.campaignService.getCompaignStatistics(element.campaignUuid))
    });
    return this.observableList;
  }

  ngAfterViewInit() {
    this.translate.getTranslation(this.translate.currentLang).subscribe((res: string) => {
      console.log(res);
      this.columnsName = {
        campaignName: res['Campaign name'],
        companyName: res['Campany name'],
        brandName: res['Company'],
        productName: res['Brand']
      };
      this.intevalLables = {
        "This month": res['This month'],
        "Last 3 months": res['Last 3 months'],
        "Last 6 months": res['Last 6 months'],
        "Last 12 months": res['Last 12 months']
      }

      this.chRef.detectChanges();
    });
    this.translate.onLangChange.subscribe(res => {
      console.log(res);
      this.columnsName = {
        campaignName: res.translations['Campaign name'],
        companyName: res.translations['Campany name'],
        brandName: res.translations['Company'],
        productName: res.translations['Brand'],
      };
      this.intevalLables = {
        "This month": res.translations['This month'],
        "Last 3 months": res.translations['Last 3 months'],
        "Last 6 months": res.translations['Last 6 months'],
        "Last 12 months": res.translations['Last 12 months']
      }
      this.campaignService.getCompaignFlights(this.selectedCompaignId).subscribe(data => {
        this.getDateIntervalsOptions(data);
        this.chRef.detectChanges();
      });
      this.chRef.detectChanges();
    });
  }
  selectCampaign(element) {
    this.selectedCampaign = element;
    let index = this.mockedData.indexOf(element);
    this.selectedCompaignId = this.campaignsList[index].campaignUuid;
    this.location.go('/home/post-evaluation' + '/' + this.selectedCompaignId);
    this.campaignService.getCompaignFlights(this.selectedCompaignId).subscribe(data => {
      console.log(data);
      this.getDateIntervalsOptions(data);
      this.selectedInterval = 'This month';
      let obj = { value: 'This month' }
      this.getTimeInterval(obj);
      this.chRef.detectChanges();
    });
  }
  getSelectedTarget(el) {
    this.audiencetargetUuid = el.audiencetargetUuid;
    this.onChange();
  }

  getSelectedChannel(channel) {
    this.selectedChannel = channel;
    this.onChange(true);
  }

  closeSettings() {
    this.postEvalutionSettingsService.open.emit(false);
  }
  selectedIndex;

  changeSelectedTab() {
    this.sourceType.forEach(element => {
      this.channelsTabGroup._tabs._results.forEach((tab, index) => {
        if (
          element.sourceTypeUuid === this.selectedChannel.sourceTypeUuid &&
          element.name === tab.textLabel
        ) {
          this.selectedIndex = index
        }
      });
    });
  }

  onChange(channels?) {
    if (channels) {
      this.campaignSettings = {
        campaignUuid: this.selectedCompaignId,
        sourceTypeUuid: this.selectedChannel.sourceTypeUuid,
        audiencetargetUuid: this.audiencetargetUuid,
        startTimestampUtc: this.startTimestampUtc,
        endTimestampUtc: this.endTimestampUtc,
        fullWeeksOnly: this.fullWeeksOnly
      };
      this.getSettings.emit(this.campaignSettings);
    } else {
      this.campaignService.getCompaignPlaysStationsWithPlaysCount(this.selectedCompaignId).subscribe(playsStations => {
        this.playsStationsList = playsStations;
        this.selectedChannel = this.playsStationsList[0];
        this.campaignSettings = {
          campaignUuid: this.selectedCompaignId,
          sourceTypeUuid: this.selectedChannel.sourceTypeUuid,
          audiencetargetUuid: this.audiencetargetUuid,
          startTimestampUtc: this.startTimestampUtc,
          endTimestampUtc: this.endTimestampUtc,
          fullWeeksOnly: this.fullWeeksOnly
        };
        this.getSettings.emit(this.campaignSettings);
        this.chRef.detectChanges();
      });
      this.changeSelectedTab();
    }
  }
}
