import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SourceTypeService {
  url = 'http://swagger.dashboard.api.spotcheck.airplaycontrol.com:10080/api';
  constructor(private http: HttpClient) { }

  getSourceType() {
    return this.http.get(this.url + '/SourceTypes');
  }

}
