import { Component, ViewChild, Input, ChangeDetectorRef, OnInit, AfterViewInit, OnDestroy, OnChanges } from "@angular/core";
import { MatPaginator, MatTableDataSource, MatDialog, MatSort } from "@angular/material";
import { FormControl } from "@angular/forms";
import { PostEvalutionSettingsService } from "app/shared/services/post-evalution-settings.service";
import { TranslateService } from "@ngx-translate/core";
import { CampaignService } from "app/shared/services/main/campaign.service";
import { PostEvalService } from "app/shared/services/main/post-eval.service";

import * as moment from 'moment';
import * as jsPDF from 'jspdf';
import * as autoTable from 'jspdf-autotable';


@Component({
  selector: 'app-post-evaluation-tab-channel',
  templateUrl: './post-evaluation-tab-channel.component.html',
  styleUrls: ['./post-evaluation-tab-channel.component.scss']
})
export class PostEvaluationTabChannelComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @Input('compaignSettings') compaignSettings;

  toppings = new FormControl();
  toppingList: string[] = [];
  selectedToppingList;
  campaignStats;

  mockedData

  constructor(
    private campaignService: CampaignService,
    private chRef: ChangeDetectorRef,
    private postEvalutionSettingsService: PostEvalutionSettingsService,
    public dialog: MatDialog,
    public translate: TranslateService,
    public postEvalService: PostEvalService
  ) {

  }

  dataSource = new MatTableDataSource(this.mockedData);
  columnsToDisplay = ['sourceLogo', 'dealershipLogo', 'firstPlayTimestampUtc',
    'lastPlayTimestampUtc', 'playsCountsTotal',
    'playsCountsDayTime', 'playsCountsPrimeTime', 'playsCountsNightTime',
    'grp'
  ];
  columnsName = {
    sourceLogo: '',
    dealershipLogo: '',
    firstPlayTimestampUtc: 'First-Play',
    lastPlayTimestampUtc: 'Last-Play',
    playsCountsTotal: 'Total plays',
    playsCountsDayTime: 'Day-time',
    playsCountsPrimeTime: 'Prime-time',
    playsCountsNightTime: 'Night-Time',
    grp: 'GRP\'s'
  }
  expandedElement;
  ngOnDestroy(): void {
    // this.translate.onLangChange.unsubscribe();
  }
  getData() {
    this.postEvalService.getBySources(this.compaignSettings).subscribe(data => {
      console.log(data, 'getBySources');
      this.mockedData = [];
      let arr: any = data;
      arr.forEach(element => {
        let obj = {
          dealershipLogo: element.dealership.logo,
          dealershipName: element.dealership.name,
          sourceName: element.source.name,
          sourceLogo: element.source.logo,
          firstPlayTimestampUtc: moment(element.firstPlayTimestampUtc).format('DD/MM/YYYY HH:mm:ss'),
          grp: element.grp,
          lastPlayTimestampUtc: moment(element.lastPlayTimestampUtc).format('DD/MM/YYYY HH:mm:ss'),
          playsCountsDayTime: element.playsCounts.dayTime,
          playsCountsNightTime: element.playsCounts.nightTime,
          playsCountsPrimeTime: element.playsCounts.primeTime,
          playsCountsTotal: element.playsCounts.total,
        }
        this.mockedData.push(obj);
      });
      this.dataSource = new MatTableDataSource(this.mockedData);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      console.log(this.mockedData, 'this.mockedData')
    });
    this.campaignService.getCompaignStatistics(this.compaignSettings.campaignUuid).subscribe(data => {
      this.campaignStats = data;
      this.chRef.detectChanges();
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnChanges(changes: import("@angular/core").SimpleChanges): void {
    if (this.compaignSettings) {
      this.getData();
      this.chRef.detectChanges();
    }
  }
  ngOnInit(): void {
  }
  ngAfterViewInit() {

    this.translate.getTranslation(this.translate.currentLang).subscribe((res: string) => {
      this.columnsName =
        {
          sourceLogo: ' ',
          dealershipLogo: '',
          firstPlayTimestampUtc: res['First-Play'],
          lastPlayTimestampUtc: res['Last-Play'],
          playsCountsTotal: res['Total plays'],
          playsCountsDayTime: res['Day-time'],
          playsCountsPrimeTime: res['Prime-time'],
          playsCountsNightTime: res['Night-Time'],
          grp: res['GRPs']
        };
      this.toppingList = [];
      for (const key in this.columnsName) {
        if (this.columnsName.hasOwnProperty(key)) {
          const element = this.columnsName[key];
          this.toppingList.push(element)
        }
      }
      this.selectedToppingList = this.toppingList;
    });
    this.translate.onLangChange.subscribe(res => {
      console.log(res);
      this.columnsName = {
        dealershipLogo: '',
        sourceLogo: ' ',
        firstPlayTimestampUtc: res.translations['First-Play'],
        lastPlayTimestampUtc: res.translations['Last-Play'],
        playsCountsTotal: res.translations['Total plays'],
        playsCountsDayTime: res.translations['Day-time'],
        playsCountsPrimeTime: res.translations['Prime-time'],
        playsCountsNightTime: res.translations['Night-Time'],
        grp: res.translations['GRPs']
      };
      this.toppingList = [];
      for (const key in this.columnsName) {
        if (this.columnsName.hasOwnProperty(key)) {
          const element = this.columnsName[key];
          this.toppingList.push(element)
        }
      }
      this.selectedToppingList = this.toppingList;
      // this.chRef.detectChanges();
    });

    this.dataSource.paginator = this.paginator;
  }
  download(type) {
    let json = [];
    this.mockedData.forEach(element => {
      let row1 = Object.assign({}, element);
      delete row1['daysData'];
      delete row1['dealershipLogo'];
      delete row1['sourceLogo'];
      json.push(row1);
      // element.daysData.forEach(row2 => {
      //   json.push(row2);
      // });
    });
    let fields = Object.keys(json[0])
    if (type === 'csv') {
      let replacer = function (key, value) { return value === null ? '' : value }
      let csv = json.map(function (row) {
        return fields.map(function (fieldName) {
          return JSON.stringify(row[fieldName], replacer)
        }).join(',')
      })
      csv.unshift(fields.join(','));
      let csvContent = "data:text/csv;charset=utf-8," + csv.join('\r\n');
      var encodedUri = encodeURI(csvContent);
      var link = document.createElement("a");
      link.setAttribute("href", encodedUri);
      link.setAttribute("download", "my_data.csv");
      document.body.appendChild(link);
      link.click();

    } else if (type === 'pdf') {
      const doc = new jsPDF();
      let rows = [];
      json.forEach((row, index) => {
        let columns = fields;
        let newRow = [];
        columns.forEach((col, index) => {
          newRow.push([row[col]]);
        });
        rows.push(newRow);
      });
      (doc as any).autoTable(
        fields,
        rows,
        {
          styles: { fontSize: 6 },
        }
      );
      doc.save('Test.pdf');
    } else if (type === 'xls') {
    }
  }
  getTotalValue(data, column) {
    if (data) {
      let value: any = 0;
      data.forEach(element => {
        if (!isNaN(+element[column])) {
          value += +element[column];
        } else {
          return value = ''
        }
      });
      if (value) {
        return Math.round(value * 100) / 100;
      }
    }
  }

  columnVisible() {
    let index = 0;
    let newColumnsToDisplay = [];
    for (const key in this.columnsName) {
      this.selectedToppingList.forEach(element => {
        if (element === this.columnsName[key]) {
          newColumnsToDisplay.push(key);
        }
      });
      index++
    }
    this.columnsToDisplay = newColumnsToDisplay;
  }
  openSettings() {
    this.postEvalutionSettingsService.open.emit(true);
  }
}
