import { Component, ViewChild, Input, ChangeDetectorRef, OnInit, AfterViewInit, OnChanges } from "@angular/core";
import { MatTableDataSource, MatPaginator } from "@angular/material";
import { MatSort } from '@angular/material/sort';
import { FormControl } from "@angular/forms";
import { PostEvalutionSettingsService } from "app/shared/services/post-evalution-settings.service";
import { MatDialog } from '@angular/material/dialog';
import { ModalVideoComponent } from "app/shared/components/modals/modal-video/modal-video.component";
import { CampaignService } from "app/shared/services/main/campaign.service";
import { PostEvalService } from "app/shared/services/main/post-eval.service";
import { TranslateService } from "@ngx-translate/core";

import * as moment from 'moment';
import * as jsPDF from "jspdf";
import * as autoTable from 'jspdf-autotable';


@Component({
  selector: 'app-post-evaluation-tab-pre-post-matching',
  templateUrl: './post-evaluation-tab-pre-post-matching.component.html',
  styleUrls: ['./post-evaluation-tab-pre-post-matching.component.scss']
})
export class PostEvaluationTabPrePostMatchingComponent implements OnInit, AfterViewInit, OnChanges {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  @Input('compaignSettings') compaignSettings;

  toppings = new FormControl();
  toppingList: string[] = [];
  selectedToppingList;
  campaignStats;

  mockedData;

  constructor(
    private campaignService: CampaignService,
    private chRef: ChangeDetectorRef,
    private postEvalutionSettingsService: PostEvalutionSettingsService,
    public dialog: MatDialog,
    public translate: TranslateService,
    public postEvalService: PostEvalService
  ) {

  }
  dataSource = new MatTableDataSource(this.mockedData);
  columnsToDisplay = ['sourceLogo', 'dealershipLogo', 'audio',
    'spotName', 'sheduledDateTime',
    'detectedDateTime', 'timeDifference', 'notes'
  ];
  columnsName = {
    sourceLogo: ' ',
    dealershipLogo: '',
    audio: 'Audio',
    spotName: 'Spot name',
    sheduledDateTime: 'Sheduled Date/Time',
    detectedDateTime: 'Detected Date/Time',
    timeDifference: 'Time Difference',
    notes: 'Notes',
  }
  expandedElement;

  getData() {
    this.postEvalService.getDayToDay(this.compaignSettings).subscribe(data => {
      console.log(data, 'getDayToDay');
      this.mockedData = [];
      let arr: any = data;
      arr.forEach(element => {
        let obj = {
          sourceLogo: element.source.logo,
          dealershipLogo: element.dealership.logo,
          audio: 'assets/examples/audio/y2mate.com-gotye_somebody_that_i_used_to_know_feat_kimbra_official_music_video_8UVNT4wvIGY.mp3',
          spotName: element.amr,
          sheduledDateTime:  moment(element.timestampUtc).format('DD/MM/YYYY HH:mm:ss'),
          detectedDateTime:  moment(element.timestampUtc).format('DD/MM/YYYY HH:mm:ss'),
          timeDifference: moment(element.timestampUtc).format('HH:mm:ss'),
          notes: element.amr
        }
        this.mockedData.push(obj);
      });
      this.dataSource = new MatTableDataSource(this.mockedData);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;

      console.log(this.mockedData, 'this.mockedData')
    });
    this.campaignService.getCompaignStatistics(this.compaignSettings.campaignUuid).subscribe(data => {
      this.campaignStats = data;
      this.chRef.detectChanges();
    });
  }

  ngAfterViewInit() {
    this.translate.getTranslation(this.translate.currentLang).subscribe((res: string) => {
      this.columnsName = {
        sourceLogo: ' ',
        dealershipLogo: '',
        audio: res['Audio'],
        spotName: res['Spot name'],
        sheduledDateTime: res['Sheduled Date/Time'],
        detectedDateTime: res['Detected Date/Time'],
        timeDifference: res['Time Difference'],
        notes: res['Notes']
      };
      this.toppingList = [];
      for (const key in this.columnsName) {
        if (this.columnsName.hasOwnProperty(key)) {
          const element = this.columnsName[key];
          this.toppingList.push(element)
        }
      }
      this.selectedToppingList = this.toppingList;
    });
    this.translate.onLangChange.subscribe(res => {
      console.log(res);
      this.columnsName = {
        sourceLogo: ' ',
        dealershipLogo: '',
        audio: res.translations['Audio'],
        spotName: res.translations['Spot name'],
        sheduledDateTime: res.translations['Sheduled Date/Time'],
        detectedDateTime: res.translations['Detected Date/Time'],
        timeDifference: res.translations['Time Difference'],
        notes: res.translations['Notes']
      };
      this.toppingList = [];
      for (const key in this.columnsName) {
        if (this.columnsName.hasOwnProperty(key)) {
          const element = this.columnsName[key];
          this.toppingList.push(element)
        }
      }
      this.selectedToppingList = this.toppingList;
      // this.chRef.detectChanges();
    });
    this.dataSource.paginator = this.paginator;
  }
  ngOnChanges(changes: import("@angular/core").SimpleChanges): void {
    if (this.compaignSettings) {
      this.getData();
      this.chRef.detectChanges();
    }
  }
  ngOnInit(): void {
  }

  columnVisible() {
    let index = 0;
    let newColumnsToDisplay = [];
    for (const key in this.columnsName) {
      this.selectedToppingList.forEach(element => {
        if (element === this.columnsName[key]) {
          newColumnsToDisplay.push(key);
        }
      });
      index++
    }
    this.columnsToDisplay = newColumnsToDisplay;
  }
  openSettings() {
    this.postEvalutionSettingsService.open.emit(true);
  }

  download(type) {
    let json = [];
    this.mockedData.forEach(element => {
      let row1 = Object.assign({}, element);
      delete row1['daysData'];
      delete row1['dealershipLogo'];
      delete row1['sourceLogo'];
      delete row1['audio'];
      json.push(row1);
      // element.daysData.forEach(row2 => {
      //   json.push(row2);
      // });
    });
    let fields = Object.keys(json[0])
    if (type === 'csv') {
      let replacer = function (key, value) { return value === null ? '' : value }
      let csv = json.map(function (row) {
        return fields.map(function (fieldName) {
          return JSON.stringify(row[fieldName], replacer)
        }).join(',')
      })
      csv.unshift(fields.join(','));
      let csvContent = "data:text/csv;charset=utf-8," + csv.join('\r\n');
      var encodedUri = encodeURI(csvContent);
      var link = document.createElement("a");
      link.setAttribute("href", encodedUri);
      link.setAttribute("download", "my_data.csv");
      document.body.appendChild(link);
      link.click();

    } else if (type === 'pdf') {
      const doc = new jsPDF();
      let rows = [];
      json.forEach((row, index) => {
        let columns = fields;
        let newRow = [];
        columns.forEach((col, index) => {
          newRow.push([row[col]]);
        });
        rows.push(newRow);
      });
      (doc as any).autoTable(
        fields,
        rows,
        {
          styles: { fontSize: 6 },
        }
      );
      doc.save('Test.pdf');
    } else if (type === 'xls') {
    }
  }

  getTotalValue(data, column) {
    if (data) {
      let value: any = 0;
      data.forEach(element => {
        if (!isNaN(+element[column])) {
          value += +element[column];
        } else {
          return value = ''
        }
      });
      if (value) {
        return Math.round(value * 100) / 100;
      }
    }
  }


  openDialog(source, el): void {
    const dialogRef = this.dialog.open(ModalVideoComponent, {
      width: '600px',
      data: {
        source: source,
        title: el['spotName'],
        list: false,
        type: "audio"
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
    });
  }

}