import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AudienceService {
  url = 'http://swagger.dashboard.api.spotcheck.airplaycontrol.com:10080/api';

  constructor(private http: HttpClient) { }

  getAudience() {
    return this.http.get(this.url + '/AudienceTargets');
  }

}
