import { Component, ViewChild, Input, AfterViewInit, OnInit, ChangeDetectorRef, OnChanges } from "@angular/core";
import { animate, state, style, transition, trigger } from '@angular/animations';
import { MatPaginator, MatTableDataSource, MatSort } from "@angular/material";
import { FormControl } from '@angular/forms';
import { PostEvalutionSettingsService } from "app/shared/services/post-evalution-settings.service";
import { CampaignService } from "app/shared/services/main/campaign.service";
import { TranslateService } from "@ngx-translate/core";
import { PostEvalService } from "app/shared/services/main/post-eval.service";

import * as moment from 'moment';
import * as jsPDF from "jspdf";
import * as autoTable from 'jspdf-autotable';

@Component({
  selector: 'app-post-evaluation-tab-date',
  templateUrl: './post-evaluation-tab-date.component.html',
  styleUrls: ['./post-evaluation-tab-date.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})

export class PostEvaluationTabDateComponent implements OnInit, AfterViewInit, OnChanges {
  
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @Input('compaignId') compaignId;
  @Input('compaignSettings') compaignSettings;

  toppings = new FormControl();
  toppingList: string[] = [];
  selectedToppingList;
  campaignStats;
  mockedData = [];
  dataSource = new MatTableDataSource(this.mockedData);

  columnsToDisplay = ['week', 'sourcesCount',
    'spotsCount', 'playsCountsTotal',
    'playsCountsDayTime', 'playsCountsPrimeTime', 'playsCountsnNightTime',
    'grp'
  ];

  columnsName = {
    week: 'Week',
    sourcesCount: 'Channels',
    spotsCount: 'Spots',
    playsCountsTotal: 'Total plays	',
    playsCountsDayTime: 'Day-time',
    playsCountsPrimeTime: 'Prime-time',
    playsCountsnNightTime: 'Night-Time',
    grp: 'GRP\'s'
  }
  expandedElement;

  constructor(
    private postEvalutionSettingsService: PostEvalutionSettingsService,
    private campaignService: CampaignService,
    private chRef: ChangeDetectorRef,
    public translate: TranslateService,
    public postEvalService: PostEvalService,
  ) {

  }
  getMaTable(data) {
    return new MatTableDataSource(data);
  }
  ngOnChanges(changes: import("@angular/core").SimpleChanges): void {
    if (this.compaignSettings) {
      this.getData();
      this.chRef.detectChanges();
    }
  }
  download(type) {
    let json = [];
    this.mockedData.forEach(element => {
      let row1 = Object.assign({}, element);
      delete row1['daysData'];
      json.push(row1);
      element.daysData.forEach(row2 => {
        json.push(row2);
      });
    });
    let fields = Object.keys(json[0])
    if (type === 'csv') {
      let replacer = function (key, value) { return value === null ? '' : value }
      let csv = json.map(function (row) {
        return fields.map(function (fieldName) {
          return JSON.stringify(row[fieldName], replacer)
        }).join(',')
      })
      csv.unshift(fields.join(','));
      let csvContent = "data:text/csv;charset=utf-8," + csv.join('\r\n');
      var encodedUri = encodeURI(csvContent);
      var link = document.createElement("a");
      link.setAttribute("href", encodedUri);
      link.setAttribute("download", "my_data.csv");
      document.body.appendChild(link);
      link.click();

    } else if (type === 'pdf') {
      const doc = new jsPDF();
      let rows = [];
      json.forEach((row, index) => {
        let columns: any = Object.keys(row);
        let newRow = [];
        columns.forEach(col => {
          newRow.push([row[col]]);
        });
        rows.push(newRow);
      });
      (doc as any).autoTable(
        fields,
        rows,
        {
          styles: { fontSize: 6 },
        }
      );
      doc.save('Test.pdf');
    } else if(type === 'xls'){
    }
  }
  getData() {
    this.postEvalService.getByWeeks(this.compaignSettings).subscribe(data => {
      console.log(data, 'getByWeeks');
      this.mockedData = [];
      let arr: any = data;
      arr.forEach(element => {
        let obj = {
          grp: element.grp,
          playsCountsDayTime: element.playsCounts.dayTime,
          playsCountsnNightTime: element.playsCounts.dayTime,
          playsCountsPrimeTime: element.playsCounts.dayTime,
          playsCountsTotal: element.playsCounts.dayTime,
          sourcesCount: element.sourcesCount,
          spotsCount: element.spotsCount,
          daysData: [],
          week: 'from ' + moment(element.weekStartTimestampUtc).format('DD/MM/YYYY') + ' to ' + moment(element.weekEndTimestampUtc).format('DD/MM/YYYY')
        }
        element.daysData.forEach(subElement => {
          let subObj = {
            grp: subElement.grp,
            playsCountsDayTime: subElement.playsCounts.dayTime,
            playsCountsnNightTime: subElement.playsCounts.dayTime,
            playsCountsPrimeTime: subElement.playsCounts.dayTime,
            playsCountsTotal: subElement.playsCounts.dayTime,
            sourcesCount: subElement.sourcesCount,
            spotsCount: subElement.spotsCount,
            week: moment(element.dayStartTimestampUtc).format('DD/MM/YYYY')
          }
          obj.daysData.push(subObj)
        });
        this.mockedData.push(obj);
      });
      this.dataSource = new MatTableDataSource(this.mockedData);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      console.log(this.mockedData, 'this.mockedData')
    });
    this.campaignService.getCompaignStatistics(this.compaignSettings.campaignUuid).subscribe(data => {
      this.campaignStats = data;
      this.chRef.detectChanges();
    });
  }

  ngAfterViewInit() {

    this.translate.getTranslation(this.translate.currentLang).subscribe((res: string) => {
      this.columnsName = {
        week: res['Week'],
        sourcesCount: res['Channels'],
        spotsCount: res['Spots'],
        playsCountsTotal: res['Total plays'],
        playsCountsDayTime: res['Day-time'],
        playsCountsPrimeTime: res['Prime-time'],
        playsCountsnNightTime: res['Night-Time'],
        grp: res['GRPs']
      };
      this.toppingList = [];
      for (const key in this.columnsName) {
        if (this.columnsName.hasOwnProperty(key)) {
          const element = this.columnsName[key];
          this.toppingList.push(element)
        }
      }
      this.selectedToppingList = this.toppingList;
    });
    this.translate.onLangChange.subscribe(res => {
      console.log(res);
      this.columnsName = {
        week: res.translations['Week'],
        sourcesCount: res.translations['Channels'],
        spotsCount: res.translations['Spots'],
        playsCountsTotal: res.translations['Total plays'],
        playsCountsDayTime: res.translations['Day-time'],
        playsCountsPrimeTime: res.translations['Prime-time'],
        playsCountsnNightTime: res.translations['Night-Time'],
        grp: res.translations['GRPs']
      };
      this.toppingList = [];
      for (const key in this.columnsName) {
        if (this.columnsName.hasOwnProperty(key)) {
          const element = this.columnsName[key];
          this.toppingList.push(element)
        }
      }
      this.selectedToppingList = this.toppingList;
      // this.chRef.detectChanges();
    });

  }

  getTotalValue(data, column) {
    let value = 0;
    data.forEach(element => {
      if (!isNaN(+element[column])) {
        value += +element[column];
      }
    });
    return value;
  }

  ngOnInit(): void {
    console.log(this.compaignId);
    if (this.compaignId) {
      this.campaignService.getCompaignStatistics(this.compaignId).subscribe(data => {
        this.campaignStats = data;
        this.chRef.detectChanges();
      });
    }
  }
  columnVisible() {
    let index = 0;
    let newColumnsToDisplay = [];
    for (const key in this.columnsName) {
      this.selectedToppingList.forEach(element => {
        if (element === this.columnsName[key]) {
          newColumnsToDisplay.push(key);
        }
      });
      index++
    }
    this.columnsToDisplay = newColumnsToDisplay;
  }
  openSettings() {
    this.postEvalutionSettingsService.open.emit(true);
  }
}